.about-me-container{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0px 0 0 0;
    background-color: white;

}

.about-me-parent{

    max-width: 1000px;
    width: 70%;
}

.about-me-card{

    display: flex;
    width: 100;
    box-shadow: 0 0px 20px -2px #1f2235;
}
.about-me-profile{

    width: 50%;
    background-image: url('../../../Pictures/me.jpg');
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.about-me-details{
    width: 48%;
    text-align: justify;
    padding: 30px;
}

.about-me-description{
    font-size: 12px;
    font-family: "Poppins Bold";
}

.about-me-highlights{

    margin: 80px 0;
  
}

.highlight-heading{

    font-family: "Poppins Bold";
    font-size: 14px;
    margin: 0 0 5px 0;

}
 
.highlight{

    display: flex;
    align-items: center;
    margin: 5px 0;
    font-size: 12px;
}

.highlight-blob{
   
    height: 12px;
    width: 12px;
    margin: 0 10px 0 0;
    background-color:  #f9a825;
    border-radius: 50%;
}
.about-me-options .highlighted-btn{
    margin: 0 0 0 30px;
  
}
.about-me-options {
    margin-top: 20%;
}


