.profile-container{

    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 10%;
    min-height: fit-content;
    background-image: url('../../../Pictures/bg11.png');
   
}

.profile-parent{

    display: flex;
    align-items: center;
    color: aliceblue;
}

.profile-details-name{
    font-size: 24px;
    font-family: "Poppins SemiBold";
}

.profile-details-role{
    display: flex;
    flex-direction: column;
    margin: 14px 0 24px 0;
}

.profile-details-role h1{
    font-size: 40px;
    font-family: cursive;
}

.primary-text{
    color: (--white);
}

.highlighted-text{
 color: #f9a825;
}

.profile-role-tagline{
    font-size: 19px;
    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile-options .highlighted-btn{

    margin: 0 0 0 28px;
    
}

.profile-options .highlighted-btn:hover{
    margin: 0 0 0 28px;
    background-color: cornsilk;
    color: #111;

}

.profile-picture{
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 10px 0px #f9a825;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin: 0 0px 0 100px;
    margin-bottom: 50px;
   
    
}

.profile-picture-background {

    
    height: 92%;
    width: 92%;
    background-image: url('../../../Pictures/me.jpg');
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;

}

.profile-picture-background:hover{

    transform: scale(1.07);
    transition: 1s ease-out;
}

.colz .colz-icon i{
    color: white;
    padding: 8px;
    padding-left: 0px;
}

.colz .colz-icon i:hover{

    transform: scale(1.5);
}

