@media screen and (max-width: 1024px){

    .profile-parent{
       display: flex;
    }
    
    .profile-picture{
       margin-right: 3%;
       width: 70%;
    }
    .profile-options{
        display: block;
     
    }
    .profile-details{
        margin: 15px 0 0;
        width: 70%;
      
    }
    .profile-options .highlighted-btn{
        margin: 10px !important;
    }
    .profile-details-role h1{
        font-size: 20px;
        font-family: cursive;
    }
    .profile-details-name{
        font-size: 20px;
        font-family: "Poppins SemiBold";
    }
}

@media screen and (max-width: 600px){

    .profile-parent{
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }

    .profile-picture{
        margin: 0 !important;
    }
    .profile-options{
        display: block;
     
    }
    .profile-details{
        margin: 25px 0 0;
        width: 50%;
      
    }

    .profile-options .highlighted-btn{
        margin: 10px !important;
    }
    .profile-details-role h1{
        font-size: 20px;
        font-family: cursive;
    }
    .profile-details-name{
        font-size: 20px;
        font-family: "Poppins SemiBold";
    }
    
}


@media only screen and (max-width: 768px){

    .profile-parent{
       display: flex;
    }
    
    .profile-picture{
       margin-right: 2%;
       width: 90%;
    }
    .profile-options{
        display: block;
     
    }
    .profile-details{
        margin: 15px 0 0;
        margin-left: 25px;
        width: 80%;
      
    }
    .profile-options .highlighted-btn{
        margin: 10px !important;
    }
    .profile-details-role h1{
        font-size: 20px;
        font-family: cursive;
    }
    .profile-details-name{
        font-size: 20px;
        font-family: "Poppins SemiBold";
    }
}